"use client"
import React, { useState } from "react";
import { Box, Container, Typography } from "@mui/material";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";

// Custom marker icon
const customIcon = new L.Icon({
  iconUrl: "/images/marker-icon.png", // Replace with your custom marker image
  iconSize: [25, 41],
  iconAnchor: [12, 41],
});

export default function LookingFor() {
  const [selectedProperty, setSelectedProperty] = useState(null);

  const properties = [
    {
      id: 1,
      title: "Diamond Manor Apartment",
      address: "22-05 Astoria Blvd, Astoria",
      beds: 2,
      baths: 1,
      sqft: 500,
      position: [40.73061, -73.935242], // Example coordinates
    },
    {
      id: 2,
      title: "Luxury Studio Apartment",
      address: "Great Neck, NY",
      beds: 1,
      baths: 1,
      sqft: 300,
      position: [40.800659, -73.725992],
    },
    // Add more properties here
  ];

  return (
    <Box
      sx={{
        position: "relative",
        overflow: "hidden",
        mb: 3,
      }}
    >
      {/* Background Image Container */}
      <Box
        sx={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: {
            xs: "50%",
            sm: "40%",
            md: "70%",
          },
          backgroundImage: 'url(/images/LookingFor.svg)',
          backgroundSize: "cover",
          backgroundPosition: "center",
          zIndex: 1,
        }}
      ></Box>

      {/* Content Overlay */}
      <Container
        sx={{
          position: "relative",
          zIndex: 2,
          paddingTop: {
            xs: "20%",
            sm: "15%",
            md: "5%",
          },
          color: "#fff",
        }}
      >
        <Typography
          variant="h4"
          sx={{
            textAlign: "center",
            marginBottom: 2,
            fontSize: {
              xs: "1.5rem",
              sm: "2rem",
              md: "2.5rem",
            },
          }}
        >
          What are you Looking For?
        </Typography>
        <Typography
          variant="body2"
          sx={{
            textAlign: "center",
            marginBottom: 4,
            fontSize: {
              xs: "0.9rem",
              sm: "1rem",
              md: "1.2rem",
            },
          }}
        >
          Lorem ipsum dolor sit amet, consectetur adipiscing elit
        </Typography>

        {/* Map */}
        <Box
          sx={{
            position: "relative",
            overflow: "hidden",
            height: "500px",
            boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.25)",
            borderRadius: 2,
            border: "5px solid #fff",
          }}
        >
          <MapContainer
            center={[40.73061, -73.935242]} // Initial center position
            zoom={12}
            style={{ height: "100%", width: "100%" }}
          >
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution="&copy; OpenStreetMap contributors"
            />
            {properties.map((property) => (
              <Marker
                key={property.id}
                position={property.position}
                icon={customIcon}
                eventHandlers={{
                  click: () => {
                    setSelectedProperty(property);
                  },
                }}
              />
            ))}
          </MapContainer>

          {/* Popup */}
          {selectedProperty && (
            <Box
              sx={{
                position: "absolute",
                top: "10%",
                left: "50%",
                transform: "translateX(-50%)",
                backgroundColor: "#fff",
                borderRadius: 2,
                boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.25)",
                padding: 2,
                zIndex: 1000,
                width: "300px",
              }}
            >
              <Typography variant="h6">{selectedProperty.title}</Typography>
              <Typography variant="body2">{selectedProperty.address}</Typography>
              <Typography variant="body2">
                Beds: {selectedProperty.beds} | Baths: {selectedProperty.baths} | Sqft: {selectedProperty.sqft}
              </Typography>
              <Box
                sx={{
                  textAlign: "right",
                  marginTop: 1,
                  cursor: "pointer",
                }}
                onClick={() => setSelectedProperty(null)}
              >
                Close
              </Box>
            </Box>
          )}
        </Box>
      </Container>
    </Box>
  );
}
